<template>
    <div class="diary-container">
        <div id="diary_turn_container">
             <div class="diary-turn-item" v-for="(item, index) in 10" :key="index">
                <div class="diary-left">
                    <div class="diary-info">
                        <div class="title">{{index}}</div>
                        <div class="desc">202331</div>
                    </div>
                    <div class="diary-info">
                        <div class="title"></div>
                        <div class="desc"></div>
                    </div>
                    <div class="diary-info">
                        <div class="title"></div>
                        <div class="desc"></div>
                    </div>
                    <div class="diary-info">
                        <div class="title">ID</div>
                        <div class="desc">faker</div>
                    </div>
                    <div class="diary-info">
                        <div class="title"></div>
                        <div class="desc"></div>
                    </div>
                    <div class="diary-info">
                        <div class="title">/</div>
                        <div class="desc">5/10</div>
                    </div>
                    <div class="diary-info">
                        <div class="title"></div>
                        <div class="desc">-50</div>
                    </div>
                    
                </div>
                <div class="diary-rigth">

                    <p class="p">
                        
                        
                        
                        
                        </p>

                    <img class="diary-img" src='https://img0.baidu.com/it/u=913981874,2619394631&fm=253&fmt=auto?w=1422&h=800' />
                    
                    <!-- <p class="p rate"></p> -->
                    <p class="p red"></p>  
                    <p class="p red-list">theshyUZI</p>
                    <p class="p red-list">theshyUZI</p>
                    <p class="p red-list">theshyUZI</p>
                    <p class="p black"></p>  
                    <p class="p black-list">
                        bangrookie
                    </p> 
                </div>  

            </div>
        </div>
       
        
    </div>
</template>

<script type="text/ecmascript-6">
    import $ from 'jquery';
    import turn from "@/util/turn.js";
    export default {
        name: '',
        components: {},
        data(){
            return {}
        },
        created() {

        },
        mounted() {
            this.onTurn();
        },
        methods: {
            onTurn() {
            var _that = this;
            this.$nextTick(() => {
                //
                var w = $(".diary-container").width();
                var h = $(".diary-container").height();
                $("#diary_turn_container").turn({
                acceleration: true, //,
                display: "single", //single=double=
                duration: 500, // 
                page: 1, // 
                pages: 2,
                gradients: true, // 
                autoCenter: true, //
                turnCorners: "bl,br", // ( 4) bl,br   tl,tr   bl,tr
                width: w,
                height: h,
                when: {
                    turned: function (e, page, view) {
                        console.log(e, page, view);
                        // if (page == 2) {
                            // _that.$router.push({ path: "/zchb/contents", query: { id: 1 } });
                        // }
                    },
                },
                });
            });
            }
        },
       
    }
</script>

<style scoped lang="less">
    @import 'diary';
</style>



